
'use strict';

var mount = require('koa-mount');
var staticCache = require('koa-static-cache');

module.exports = function (opts, app) {
  var options = opts.staticOpts;

  return mount(options.router, staticCache(options.dir, {
    maxAge: options.maxage,
    buffer: true
  }));
};


